<template>
  <b-dialog
    :dialogVisible="dialogVisible"
    :title="title"
    :toolbar="toolbar"
    @close="cance"
  >
    <el-form label-width="100px" :model="dialogForm" :rules="rules" ref="form">
      <el-form-item label=":" prop="name">
        <el-input v-model="dialogForm.name"></el-input>
      </el-form-item>
      <el-form-item label=":" prop="url">
        <el-input v-model="dialogForm.url"></el-input>
      </el-form-item>
      <el-form-item label=":" prop="class">
        <b-select
          v-model="dialogForm.class"
          :options="classOptions"
          :property="menuProperty"
        ></b-select>
      </el-form-item>
    </el-form>
  </b-dialog>
</template>

<script>
  import BDialog from 'components/common/b-dialog.vue'
  import BSelect from 'components/common/b-select.vue'
  import req from 'api/web'

  export default {
    name: 'TableDialog',
    components: {
      BDialog,
      BSelect
    },
    props: {
      title: String,
      dialogForm: Object,
      value: Boolean
    },
    data () {
      return {
        toolbar: [
          {
            type: 'plane',
            name: '',
            func: () => {
              this.cance()
            }
          },
          {
            type: 'primary',
            name: '',
            func: () => {
              this.submit()
            }
          }
        ],
        dialogVisible: this.value,
        menuProperty: {
          label: 'className',
          value: 'className'
        },
        rules: {
          name: [
            {required: true, message: ''}
          ],
          url: [
            {required: true, message: ''}
          ],
          class: [
            {required: true, message: ''}
          ]
        }
      }
    },
    computed: {
      classOptions () {
        return this.$store.state.menu.menuData.find(item => item.route === '/website').children
      }
    },
    methods: {
      cance () {
        this.dialogVisible = false
        this.$emit('input', false)
      },
      submit () {
        this.$refs.form.validate(valid => {
          if (valid) {
            this.title.includes('')
              ? this.reqFunc('addWebsite', '')
              : this.reqFunc('updateWebsite', '')
          }
        })
      },
      reqFunc (reqAddress, status) {
        req(reqAddress, this.dialogForm)
          .then((res) => {
            if (res.affectedRows) {
              this.$message({type: 'success', message: `${status}`})
              this.$emit('fetch')
              this.cance()
            } else {
              this.$message({type: 'error', message: `${status}`})
            }
          })
      }
    }
  }
</script>

<style lang="scss" scoped>

</style>




