<template>
  <div>
    <b-table
      ref="bTable"
      :table="table"
      @handleSizeChange="handleSizeChange"
    >
    <template slot-scope="tableScope">
      <el-table
        v-loading="loading"
        :data="tableScope.data"
        :height="tableScope.height"
        border
        stripe
      >
        <el-table-column type="selection" width="40"></el-table-column>
        <el-table-column
          v-for="(item, key) in tableScope.column"
          :key="key"
          :prop="item.prop"
          :label="item.label"
          :width="item.width"
          showOverflowTooltip
        >
        </el-table-column>
        <b-table-operator :table="tableScope"></b-table-operator>
      </el-table>
    </template>
    </b-table>
    <table-dialog
      v-if="dialogVisible"
      v-model="dialogVisible"
      :title="title"
      :dialog-form="dialogForm"
      @fetch="fetch({pageIndex: 1, pageSize: table.info.pageSize})"
    ></table-dialog>
  </div>
</template>

<script>
import {cloneData} from 'util'
import req from 'api/web'
import BTable from 'components/common/b-table/index.vue'
import BTableOperator from 'components/common/b-table-operator/index.vue'
import TableDialog from './table-dialog.vue'
export default {
  name: 'Table',
  components: {
    BTable,
    BTableOperator,
    TableDialog
  },
  data () {
    return {
      table: {
        column: [
          {prop: 'id', label: 'ID', width: '50'},
          {prop: 'class', label: '', width: '150'},
          {prop: 'name', label: '', width: '150'},
          {prop: 'orderNo', label: '', width: '100'},
          {prop: 'subclass', label: '', width: '100'},
          {prop: 'url', label: '', width: '200'}
        ],
        setting: {
          operator: [
            {
              text: '',
              func: (row) => {
                this.edit(row)
              }
            },
            {
              text: '',
              func: (row) => {
                this.delete(row)
              }
            }
          ],
          toolbar: [
            {
              text: '',
              type: 'primary',
              func: this.add
            },
            {
              text: '',
              type: 'danger',
              func: (vm) => {
                this.delete()
              }
            }
          ],
          export: {
            text: '',
            type: 'success',
            req,
            reqApi: 'getWebsite'
          }
        },
        data: [],
        info: {
          pageIndex: 1,
          pageSize: 8,
          total: 0
        }
      },
      loading: false,
      dialogForm: {},
      dialogVisible: false
    }
  },
  created () {
    this.fetch(this.table.info)
  },
  methods: {
    handleSizeChange (info) {
      this.fetch(info)
    },
    fetch (options) {
      this.loading = true
      req('getWebsite', options)
        .then(res => {
          this.table.data = res.data
          this.table.info = res.info
          this.newTableData = cloneData(res.data)
        })
        .finally(() => (this.loading = false))
    },
    add () {
      this.dialogForm = {}
      this.title = ''
      this.dialogVisible = true
    },
    edit (row) {
      this.dialogForm = row
      this.title = ''
      this.dialogVisible = true
    },
    delete (row) {
      let deleteData = row ? [row] : this.$refs.bTable.selectData
      if (!deleteData.length) return this.$message({message: '', type: 'warning'})
      this.$confirm('?')
        .then(() => {
          req('deleteWeb', {id: JSON.stringify(deleteData.map(item => item.id))})
            .then(data => {
              if (data.affectedRows) {
                this.$message({type: 'success', message: ''})
                this.fetch({pageIndex: 1, pageSize: this.table.info.pageSize})
              } else {
                this.$message({type: 'error', message: ''})
              }
            })
            .catch(err => {
              console.log(err)
            })
        })
    }
  }
}
</script>



